import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';
import './App.css';
import {MoviesList} from "./components/MoviesList"
import {MovieDetail} from "./components/MovieDetail"

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/movie/:id">
            <MovieDetail/>
          </Route>
          <Route path="/">
            <MoviesList></MoviesList>
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
